html{
    min-height: 100%;
}

body{
    min-height: 100%;
}

.main{
    display: grid;
    grid-template-columns: 15% 70% 15%;
}

.messages{
    display: grid;
    grid-column-start: 2;
    grid-column-end: 3;
    -ms-flex-line-pack: center;
        align-content: center;
}

.recieved{
    grid-row-start: 1;
    grid-row-end: 2;
    grid-column-start: 1;
    grid-column-end: 3;
    min-height: 200px;
    max-height: 200px;
    overflow: scroll;
}

#recieved-wrapper{
    grid-template-rows: 100vh;
    -ms-flex-align: center;
        align-items: center;
    display: grid;
    grid-template-rows: auto auto;
    grid-template-columns: auto 50px;
}

.type-in{
    grid-row-start: 2;
    grid-row-end: 3;
    grid-column-start: 1;
    grid-column-end: 2;
    -ms-flex-item-align: stretch;
        align-self: stretch;
    overflow: scroll;
}

.send-msg{
    grid-row-start: 2;
    grid-row-end: 3;
    grid-column-start: 2;
    grid-column-end: 3;
    -ms-flex-item-align: stretch;
        align-self: stretch;
}


